#board {

}

.rotateBoardOut {
  -webkit-animation-name: rotateBoardOut;
          animation-name: rotateBoardOut;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

.rotateBoardIn {
  -webkit-animation-name: rotateBoardIn;
          animation-name: rotateBoardIn;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

@-webkit-keyframes rotateBoardOut {
  0% {-webkit-transform: rotateY(0deg);transform: rotateY(0deg)}
  100% {-webkit-transform: rotateY(90deg);transform: rotateY(90deg)}
}

@keyframes rotateBoardOut {
  0% {-webkit-transform: rotateY(0deg);transform: rotateY(0deg)}
  100% {-webkit-transform: rotateY(90deg);transform: rotateY(90deg)}
}

@-webkit-keyframes rotateBoardIn {
  0% {-webkit-transform: rotateY(90deg);transform: rotateY(90deg)}
  100% {-webkit-transform: rotateY(0deg);transform: rotateY(0deg)}
}

@keyframes rotateBoardIn {
  0% {-webkit-transform: rotateY(90deg);transform: rotateY(90deg)}
  100% {-webkit-transform: rotateY(0deg);transform: rotateY(0deg)}
}



